.nav {
  background-color: #00adb5;
  padding-top: 10px;
}

.navItems a {
  width: 100px;
  margin: 10px;
  padding: 5px;
  border-radius: 50px;
  cursor: pointer;
  color: #eeeeee;
  background-color: #393e46;
  text-decoration: none;
  transition: 1s all;
}
.navItems a:hover {
  background-color: #222831;
}

.navBtnsOn,
.navBtnsOff {
  display: flex;
  flex-wrap: wrap;
  justify-content: space-around;
  width: 50%;
  min-width: 200px;
  margin: 0 auto;
  border-radius: 50px;
  background-color: #393e46;
  transition: 1.5s all;
}
.navBtnsOn {
  opacity: 1;
}
.navBtnsOff {
  opacity: 0;
}
.navBtnsOn a {
  opacity: 1;
}
.navBtnsOff a {
  opacity: 0;
  margin: 0;
  pointer-events: none;
  cursor: none;
  user-select: none;
}
.menuBtn {
  font-size: 25px;
  transition: 1s;
  cursor: pointer;
}
.menuBtn:hover {
  scale: 1.2;
}
