.card {
  /* display: flex;
  flex-direction: column;
  align-items: center; */
  margin: 3px;
  padding: 5px;
  border-radius: 30px;
  color: white;
  transition: 1s;
}
.card:hover {
  background-color: rgba(0, 173, 181, 0.2);
}

.img {
  margin: 0 auto;
  width: 150px;
  height: 150px;
}

.cardItems {
  display: flex;
  justify-content: space-between;
}
.info {
  display: flex;
  flex-direction: column;
  padding-left: 10px;
}
.item {
  display: flex;
  align-items: end;
  padding-right: 10px;
}
.icon {
  font-size: 22px;
  margin: 3px;
}

h3 {
  margin-bottom: 5px;
  padding-left: 5px;
}

p {
  text-align: justify;
}

img {
  width: 150px;
  height: 150px;
  border-radius: 30px;
}
