.container {
  display: flex;
  flex-direction: column;
  padding-top: 10px;
  background-color: #222831;
  color: #eeeeee;
  overflow: auto;
}

.topBox {
  display: grid;
  grid-template-columns: repeat(auto-fit, minmax(250px, 1fr));
}
.userInfo {
  text-align: start;
  margin-left: 10px;
}
.balance {
  display: flex;
  flex-direction: column;
}
.item {
  display: flex;
}
.item div {
  width: 50%;
  margin: 5px;
  padding: 10px;
  border-radius: 10px;
  background-color: #00adb5;
}
.card {
  max-width: 100%;
  margin: 5px;
  padding: 10px;
  border-radius: 10px;
  background-color: #00adb5;
}

.middleBox {
  display: flex;
  flex-direction: column;
  height: 250px;
}

.bottomBox {
  display: grid;
  grid-template-rows: 30px 260px;
}
.btns {
  display: flex;
  flex-wrap: wrap;
  justify-content: space-around;
  width: 100%;
  margin: 0 auto;
  background-color: #00adb5;
  transition: 1.5s all;
}
.btn {
  width: 100px;
  cursor: pointer;
  color: #eeeeee;
  text-decoration: none;
  transition: 1s all;
}
.btn:hover {
  background-color: #222831;
}
.content {
  display: flex;
  justify-content: center;
  align-items: center;
}
