.container {
  display: flex;
  align-items: center;
  background-color: #00adb5;
}

.items {
  display: flex;
  flex-wrap: wrap;
  justify-content: center;
  width: 20%;
  min-width: 200px;
  margin: 10px auto;
  border-radius: 50px;
  background-color: #393e46;
  transition: 1.5s all;
}

.items a {
  width: 100px;
  border-radius: 50px;
  cursor: pointer;
  color: #eeeeee;
  background-color: #393e46;
  text-decoration: none;
  transition: 1s all;
}
